import React, { FC } from "react";
import classNames from "classnames/bind";

import { Heading, HeadingTag } from "common/components/new/Heading";
import { Text } from "common/components/Text";
import { Link } from "common/components/Link";
import { Picture } from "common/components/Picture";

import { MapType } from "./Map.types";
import styles from "./Map.module.scss";

const cx = classNames.bind(styles);

export const Map: FC<MapType> = ({
    title,
    address,
    link,
    image,
    className,
}) => {
    return (
        <section className={cx(className, "root")}>
            <div className={cx("left-column")}>
                <Heading tag={HeadingTag.H2} className={cx("title")}>
                    {title}
                </Heading>
                <Text className={cx("address")}>{address}</Text>
            </div>
            <div className={cx("link-container")}>
                <Link link={link} target="_blank" className={cx("link-map")}>
                    <Picture
                        classNameImg={cx("image")}
                        desktop={{
                            webp: image?.desktop?.webp,
                            jpeg: image?.desktop?.jpeg,
                            media: "(min-width: 768px)",
                        }}
                        mobile={{
                            webp: image?.mobile?.webp,
                            jpeg: image?.mobile?.jpeg,
                            media: "(max-width: 767px)",
                        }}
                    />
                </Link>
            </div>
        </section>
    );
};
